@import "variables";
@import "mixins";
@import "bootstrap-scss/custom";
@import "flight-list-item";

.fa-spinner {
  font-size: 120px;
  color: #203375;
}

.flights-listing-wrapper {
  .summary-wrapper {
    padding: 0 15px;
    margin-bottom: 15px;
    .col-sm {
      padding-top: 10px;
      padding-bottom: 10px;
      border: 1px solid $golden;
      border-right-width: 0;
      @media screen and (max-width: $screen-xs) {
        border-right-width: 1px;
        margin-bottom: -1px;
        margin-right: -1px;
      }
      &:nth-of-type(1) {
        min-width: 250px;
      }
      &:nth-of-type(3) {
        max-width: 100px;
      }
      &:last-of-type {
        border-right-width: 1px;
      }
    }
  }
}

.filter-box {
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 30px;
  }
  .title {
    display: block;
    background-color: $darkBlue;
    color: $white;
    padding: 10px 15px;
    font-size: 24px;
    a {
      display: block;
      width: 100%;
      position: relative;
      padding-right: 40px;
      .fa-chevron-down {
        width: 25px;
        height: 25px;
        color: $white;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
        transition: 0.3s;
      }
      &[aria-expanded="true"] {
        .fa-chevron-down {
          transform: rotate(180deg);
        }
      }
      @media screen and (min-width: $screen-sm) {
        pointer-events: none;
        .fa-chevron-down {
          display: none;
        }
      }
    }
  }
  .collapse-filters {
    @media screen and (min-width: $screen-sm) {
      display: block !important;
    }
  }
  .filter-set {
    padding: 15px 15px;
    .filter-label {
      color: $darkBlue;
      font-size: 20px;
      display: block;
      padding-bottom: 5px;
      border-bottom: 1px solid $darkBlue;
      margin-bottom: 10px;
    }
    .form-group {
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    .slider {
      margin-top: 25px !important;
      margin-bottom: 0 !important;
    }
  }
}

.suggestions-box {
  background-color: $greyE9;
  padding: 10px 15px;
  margin-bottom: 15px;
  display: flex;
  flex-wrap: wrap;
  .col-sm-4 {
    padding-left: 5px;
    padding-right: 5px;
  }
  .suggestion-item {
    display: block;
    width: 100%;
    border: 1px solid transparent;
    padding: 10px 10px;
    box-shadow: 0 9px 23px rgba(0, 0, 0, .09), 0 5px 5px rgba(0, 0, 0, .06);
    .icon-box,
    .details-box {
      display: inline-block;
      vertical-align: middle;
    }
    .icon-box {
      width: 40px;
      text-align: center;
      .fa {
        font-size: 30px;
      }
    }
    .details-box {
      width: calc(100% - 40px);
      text-align: center;
      span {
        display: block;
        font-weight: 700;
        &:first-of-type {
          font-size: 24px;
        }
      }
    }
    &.best {
      background-color: $light;
      color: $darkBlue;
    }
    &.low {
      background-color: $primary;
      color: $white;
    }
    &.fast {
      background-color: $secondary;
      color: $darkBlue;
    }
  }
}
