@import "variables";
@import "mixins";
@import "flight-list-item";
@import "bootstrap-scss/mixins";

.btn-book-now {
  background: #FF4E4E;
  padding: 10px 100px;
  margin: 0 auto;
  border: none;
  color: #fff;
  &:hover,
  &:focus {
    background: darken(#FF4E4E, 5%);
  }
}

.booking-page {
  .vdp-datepicker {
    max-width: 100% !important;
    input {
      width: 100% !important;
      max-width: 100% !important;
    }
    * {
      color: #060606 !important;
    }
  }
}
