//screen sizes
$max-container: 480px;
$screen-xs: 640px;
$screen-sm: 641px;
$screen-md: 1199px;
$mac-port: 768px;
$mac-land: 1024px;
$min-width-on-scaled-down: 1200px;
$base-font-size: 1rem;

// Line height
$line-height-base: 1.6;
// Other Sizes
$base-spacing: $line-height-base * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

/*
For naming convention of color variable I have used this site
https://www.htmlcsscolor.com/hex/262928
we have to place the hex code of color after the last slash (/), It give a nice name for the color
*/

$white: #ffffff;
$blue: #0091EA;
$black: #1e1e1f;
$golden: #d19e01;
$silver: #EDEDED;
$darkBlue:#203375;
$greyE9: #e9e9e9;
$lightF7: #f7f5f5;
$grey999: #999;
$orange: #f57b3d;
