
.fa-plane {
  &.fa-90 {
    transform: rotate(90deg);
  }
  &.fa-45 {
    transform: rotate(45deg);
  }
  &.fa-225 {
    transform: rotate(225deg);
  }
}

.flight-list-item {
  border-radius: 5px;
  box-shadow: 0 9px 23px rgba(0, 0, 0, .09), 0 5px 5px rgba(0, 0, 0, .06);
  margin-bottom: 30px;
  .airline-logo {
    display: block;
    width: 100%;
    height: auto;
    margin: auto;
    max-width: 110px;
    &.smaller {
      width: 80px;
      max-width: 80px;
      margin-left: 0;
    }
  }
  .legs-row {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .itinerary-summary {
    padding: 15px 15px;
    color: $darkBlue;
    .stoppage-column {
      text-align: center;
      .info {
        margin-bottom: 0;
      }
    }
    .info > .info {
      display: inline-block;
    }
    @media screen and (max-width: $screen-xs) {
      margin: 15px 0;
    }
    > .row {
      margin-bottom: 20px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  .flight-detail-wrapper {
    padding-top: 6px;
    padding-bottom: 6px;
    border-top: 1px solid $silver;
    border-bottom: 1px solid $silver;
    .first-column {
      @media screen and (max-width: $screen-xs) {
        padding-right: 0;
      }
    }
    .second-column {
      padding-right: 0;
      padding-left: 0;
    }
    .third-column {
      padding-left: 0;
    }
    a[data-toggle="collapse"] {
      .fa {
        transition: 0.3s;
      }
      .first {
        display: inline-block;
      }
      .second {
        display: none;
      }
      &[aria-expanded="true"] {
        .second {
          display: inline-block;
        }
        .first {
          display: none;
        }
        .fa {
          transform: rotate(180deg);
        }
      }
      &:hover, &:focus {
        span {
          text-decoration: underline;
        }
      }
    }
    .collapsing,
    .collapse.show {
      padding-top: 6px;
      margin-bottom: -6px;
    }
    .card {
      border: none;
      border-radius: 0 0 5px 5px;
      background-color: inherit;
      border-top: 1px solid $silver;
      .leg-details {
        padding: 0;
        .seg-details {
          margin-bottom: 15px;
          &:last-of-type {
            margin-bottom: 0;
            .lay-over {
              display: none;
            }
          }
          .column-c {
            .fa-plane {
              font-size: 32px;
            }
          }
        }
      }
    }
    .lay-over {
      margin-top: 15px;
      padding-top: 6px;
      padding-bottom: 6px;
      border-top: 1px dashed $black;
      border-bottom: 1px dashed $black;
    }
    .collapse.show + .collapse.show {
      .card {
        padding-top: 0;
        border-top: none;
      }
    }
    .flight-information {
      .leg-details {
        margin-bottom: 20px;
        &:after {
          display: block;
          content: "";
          margin: 20px auto 0;
          width: 100%;
          height: 4px;
          background-color: $darkBlue;
        }
        &:last-of-type {
          margin-bottom: 0;
          &:after {
            display: none;
          }
        }
        .seg-details {
          @media screen and (max-width: $screen-xs) {
            .col-sm.col-12 {
              margin-bottom: 10px;
              &:last-of-type {
                margin-bottom: 0;
              }
            }
          }
          @media screen and (min-width: $screen-sm) {
            .col {
              min-width: 220px;
              max-width: 220px;
              &.column-e,
              &.column-c {
                min-width: 100px;
                max-width: 100px;
              }
            }
          }
        }
      }
    }
  }
  .stoppage {
    width: 100%;
    height: 1px;
    position: relative;
    background: $grey999;
    padding: 0 10px;
    max-width: 100px;
    margin: 10px auto !important;
    @media screen and (max-width: $screen-xs) {
      max-width: 200px;
    }
    &:before {
      position: absolute;
      content: "";
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $grey999;
      left: 50%;
      margin-left: -5px;
      top: -5px;
    }
    &.none:before {
      display: none;
    }
  }
  .book-now {
    display: block;
    width: 100%;
    margin-top: 5px;
    border-radius: 0;
    box-shadow: -3px 3px 2px #666;
  }
  .price-table {
    thead,
    tfoot {
      background: $darkBlue;
      color: $white;
    }
    thead,
    tbody,
    tfoot {
      tr {
        td,
        th {
          &:nth-last-of-type(1) {
            text-align: right;
          }
        }
      }
    }
    tfoot {
      tr td {
        font-weight: 700;
      }
    }
  }
}

.info {
  font-size: 14px;
  line-height: 1.25;
  font-weight: 400;
  display: block;
  margin-bottom: 5px;
  &.no-wrap {
    white-space: nowrap;
  }
  &.large {
    font-size: 16px;
  }
  &.bold {
    font-weight: 700;
  }
  &.medium {
    font-weight: 600;
  }
  &:last-of-type {
    margin-bottom: 0;
  }
  .goldy,
  &.goldy {
    color: $golden;
  }
  &.separator {
    margin-left: 3px;
    margin-right: 3px;
  }
  &.price {
    font-size: 24px;
    font-weight: 700;
    text-align: center;
    color: $darkBlue;
  }
}

.todo {
  color: red !important;
}

.farebox {
  padding: 8px;
  border: 2px solid $orange;
  .farebox-heading {
    background: $orange;
    color: $white;
    font-size: 23px;
    padding: 3px;
    text-align: center;
  }
}
